:host {
  display: block;
  line-height: 1;

  .table {
    td,
    th {
      white-space: pre-wrap;
    }

    thead {
      z-index: 1;

      th {
        padding: 0.75rem 0.5rem;

        &.thead-xxs {
          width: 2.3rem;
        }
      }
    }

    &.table-sm {
      thead {
        th {
          padding: 0.5rem 0.25rem;

          &.thead-xxs {
            padding: 0.25rem;
          }
        }
      }
    }

    .form-check > .form-check-input[type=radio] {
      background-color: #fff;
      cursor: pointer;
    }
  }

  .form-check {
    padding: 0;
  }

  .table-sorter {
    cursor: pointer;

    > .cell {
      display: inline-block;
      height: 100%;
      position: relative;
      width: 100%;
      line-height: 1.1;

      &:before {
        border-top: 5px solid #000;
        bottom: .15rem;
      }

      &:after {
        border-bottom: 5px solid #000;
        border-top: 5px solid transparent;
        top: -.2rem;
      }

      &:after,
      &:before {
        content: "";
        height: 0;
        width: 0;
        position: absolute;
        right: 5px;
        border-left: 5px solid transparent;
        border-right: 5px solid transparent;
        opacity: .3;
      }
    }

    &.asc > .cell {
      &:after {
        opacity: .7;
      }

      &:before {
        opacity: 0;
      }
    }

    &.desc > .cell {
      &:before {
        opacity: .7;
      }

      &:after {
        opacity: 0;
      }
    }
  }

  .table-layout-fixed {
    table-layout: fixed;
  }


  .table-layout-auto {
    table-layout: auto;
  }

  .no-records-found {
    height: 30vh;
  }

  .fixed-table-header {
    th {
      position: sticky;
      top: 0;
    }

    thead {
      position: sticky;
      top: 0;
      background: white;

      tr {
        border-top-width: 0;
      }
    }
  }

  .table-description {
    font-size: 0.5rem;
    font-weight: normal !important;
  }

  .table-top,
  .table-bottom {
    padding: 0.5rem;

    &:after {
      clear: both;
      content: " ";
      display: table;
    }
  }

  .table-top {
    label {
      margin-bottom: 0;
    }

    .table-pageSizeMenu {
      label {
        select {
          border-color: #f0f2f5;
          border-radius: 0.375rem;
          padding: 6px;
        }
      }
    }
  }

  .table-selectedInfo,
  .table-pageSizeMenu,
  .table-pageInfo {
    float: left;
    color: #7b809a;
    font-size: .875rem;
  }

  .table-selectedInfo,
  .table-pageInfo {
    margin: 0.7rem 0;
    padding-right: 2rem;
  }

  .table-search,
  .table-pagination {
    float: right;
  }

  .table-search {
    input {
      font-size: .875rem;
      color: #495057;
      border: 1px solid #f0f2f5;
      border-radius: 0.375rem;
      padding: 6px 12px;
    }
  }

  .tr {
    &.table-sorter {
      cursor: pointer;
    }
  }

  tr {
    storm-toolbar {
      storm-button {
        .btn {
          width: 1.5rem;
          height: 1.5rem;
          padding: 0.3rem 0.3rem;
        }
      }

      &.storm-dynamic-label {
        storm-button {
          .storm-button-label {
            display: none;
          }
        }
      }
    }

    &:hover {
      storm-toolbar {
        &.storm-dynamic-label {
          storm-button {
            .btn:not(.btn-icon-only) {
              width: auto;
              height: auto;
              padding: .06rem .5rem .06rem .2rem;
            }

            .storm-button-label {
              display: block;
            }
          }
        }
      }
    }
  }
}